// action
const GET_DATA = "select/GET_DATA";

// action creator
export const getData = res => ({ type: GET_DATA, res });

// initalState
const initalState = [
  {
    title: "",
    code: "",
    contents: [
      {
        c_id: 0,
        statement: ""
      }
    ],
    tag: "",
    l_id: 0
  }
];

// reducer
const select = (state = initalState, action) => {
  switch (action.type) {
    case GET_DATA:
      return action.res;
    default:
      return state;
  }
};
// functions

// export
export default select;
